<style lang="less" scoped>
.box {
  @{global} .el-dialog__header {
    padding: 0 20px;
    height: 54px;
    display: flex;
    align-items: center;
  }
  @{global} {
    .el-dialog__footer {
      padding: 16px 20px;
      .el-button {
        min-width: 72px;
      }
    }
    .el-dialog__headerbtn {
      top: 16px;
    }
    .el-dialog__headerbtn {
      .el-dialog__close {
        color: #666;
        font-weight: bold;
      }
    }
    .el-dialog__title {
      position: relative;
      display: block;
      padding-left: 20px;
      font-weight: bold;
      font-size: 20px;
      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 4px;
        width: 4px;
        height: 16px;;
        background-color: #398CFF;
      }
    }
    .el-dialog__body {
      min-height: 400px;
      max-height: 600px;
      overflow: auto;
      padding: 20px;
      font-size: 12px;
      border-top: 1px solid #DADDE2;
      border-bottom: 1px solid #DADDE2;
      .table-filters {
        color: #666;
      }
    }
  }
}
</style>

<template>
  <div class="box">
    <el-dialog :width="_width" :title="title" @closed="closed" :visible.sync="show">
      <slot name="body"/>
      <div slot="footer" class="dialog-footer">
        <slot name="footer"/>
      </div>
    </el-dialog>
  </div>
</template>

<script>

export default {
  components: {
  },
  props: {
    width: {
      type: String,
      required: false
    },
    title: {
      type: String,
      required: true
    },
    hasDialog: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      show: false
    }
  },
  computed: {
    _width () {
      return this.width || '656px'
    }
  },
  watch: {
    hasDialog (val, oldVal) {
      if (val !== oldVal) {
        this.show = val
      }
    }
  },
  methods: {
    closed () {
      this.$emit('is-show-dialog', false)
    }
  }
}
</script>



