<style lang="less" scoped>
  .system-item {
    display: flex;
    position: relative;
    padding: 0 5px 0 48px;
    width: 132px;
    height: 36px;
    align-items: center;
    border-radius: 18px;
    border: 1px solid #E9ECF2;
    .el-icon-circle-close-outline {
      position: absolute;
      right: -9px;
      font-size: 16px;
      color: #cdcdcd;
      background-color: #fff;
    }
  }
  .system-item-title {
    width: 74px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .icon {
    position: absolute;
    left: 16px;
    width: 20px;
    height: 20px;
  }
</style>
<template>
  <a class="system-item" :title="item.name" href="javascript:;">
    <svg class="icon" aria-hidden="true">
      <use :xlink:href="'#icon-' + item.icon"></use>
    </svg>
    <i v-if="showClose" @click="onDel" class="el-icon-circle-close-outline"></i>
    <p class="system-item-title">{{item.name}}</p>
  </a>
</template>
<script>
export default {
  data () {
    return {
    }
  },
  props: {
    showClose: {
      type: Boolean,
      default: false
    },
    item: {
      type: Object,
      required: true
    }
  },
  methods: {
    onDel () {
      this.$emit('del-click', this.item.$index)
    }
  }
}
</script>



