<style lang="less" scoped>
  .filter-input-box {
    padding-bottom: 7px;
    @{global} {
      .el-input__inner {
        width: 100%;
        font-size: 12px;
      }
    }
  }
  .tree-content {
    overflow: auto;
    position: absolute;
    top: 40px;
    left: 0;
    width: 100%;
    max-height: calc(100% - 40px);
    @{global} .el-tree-node>.el-tree-node__children {
      overflow: inherit;
    }
  }
</style>
<template>
  <div @click.stop="onReturn">
    <div class="filter-input-box">
      <el-input
      ref="filterInput"
      :placeholder="placeholder"
      size="small"
      clearable
      suffix-icon="el-icon-search"
      v-model="filterText">
      </el-input>
    </div>
    <div class="tree-content">
      <el-tree
      v-show="data && data.length > 0"
      node-key="id"
      :data="data"
      :props="defaultProps"
      :filter-node-method="filterNode"
      :highlight-current="true"
      :expand-on-click-node="expandOnClickNode"
      :default-expand-all="defaultExpandAll"
      @node-click="onNodeClick"
      ref="tree">
      </el-tree>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      filterText: '',
      defaultProps: {
        children: 'children',
        label: 'name'
      }
    }
  },
  props: {
    data: {
      type: Array,
      required: true
    },
    defaultExpandAll: {
      type: Boolean,
      default: false
    },
    expandOnClickNode: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: '输入关键字进行过滤'
    }
  },
  watch: {
    filterText (val) {
      this.$refs.tree.filter(val)
    }
  },
  methods: {
    onReturn () {
      return false
    },
    onNodeClick (data, node, el) {
      this.$emit('node-click', data, node, el)
    },
    filterNode (value, data) {
      if (!value) return true
      return data.name.indexOf(value) !== -1
    }
  }
}
</script>



